import React, { useState, useEffect } from 'react';
import { Modal, Upload, Icon, message, Button, Divider, Card, Row, Col, Checkbox } from 'antd';
import { BaseUrl } from '../../../api/ApiSender';
import { getHotelImages } from '../../../api/HotelApi';
import { uploadImage } from '../../../api/ResourceApi';

const Dragger = Upload.Dragger;
const { Meta } = Card;

export default function SlideImageModal(props) {
	const [images, setImages] = useState([]);
	const [file, setFile] = useState(undefined);
  const [fileList, setFileList] = useState([]);
  const [uploading, setUploading] = useState(false);
	
	useEffect(() => {
		if (props.hotelId) {
			fetchImages(props.hotelId);
		}
	}, [props.hotelId])

	const fetchImages = async (id) => {
    getHotelImages({ hotelId: id, paging: { currentPage: 0, pageSize: 50} }).then(res => {
      if (res.status === 'SUCCESS' && res.value) {
        setImages(res.value);
      }
    })
	}
	
	const handleUpload = () => {
    if (!props.hotelId) return;

    const formData = new FormData();
    formData.append('hotelId', props.hotelId);
    formData.append('image', file);

    setUploading(true);
    uploadImage(formData).then(res => {
      setFile(undefined);
      setFileList([]);
      setUploading(false);
      if (res.status === 'SUCCESS') {
        fetchImages(props.hotelId);
        message.success('Tải lên thành công');
      } else {
        message.error(res.value || 'Tải lên không thành công');
      }
    }).catch(err => {
      setUploading(false);
      message.error(err.message);
    })
  }

  const onChangeCheckboxMainImage = (e, idx) => {
    const data = [...images];
    data.map((item, index) => {
      if (index === idx && e.target.checked) {
        item.selected = true;
      } else {
        item.selected = false;
      }
    })
    setImages(data);
	}
	
	const onClickSaveBtn = () => {
    let selectedImage = 0;
    images.map(item => {
      if (item.selected) {
        selectedImage = item.fullUrl;
      }
    });
    if (selectedImage === 0) return;
		props.onCloseModal(BaseUrl + selectedImage);
	}
	
	const options = {
    name: 'image',
    accept: ".jpg, .jpeg, .png, .gif",
    listStyle: 'picture',
    multiple: false,
    beforeUpload(data) {
      setFileList([data]);
      setFile(data);
      return false;
    },
    fileList
  };
	
	return (
		<div>
			<Modal
				title='Chọn logo khách sạn'
				visible={props.showModal}
				onOk={onClickSaveBtn}
				onCancel={props.onCloseModal}
				width={'70%'}
			>
				<Dragger {...options}>
					<p className="ant-upload-drag-icon">
						<Icon type="inbox" />
					</p>
					<p className="ant-upload-text">Click hoặc kéo thả ảnh vào đây để tải lên</p>
					{/* <p className="ant-upload-hint">Support for a single or bulk upload. Strictly prohibit from uploading company data or other band files</p> */}
				</Dragger>
				<Row style={{ marginTop: '16px' }}>
					<Button
							type="primary"
							onClick={handleUpload}
							disabled={file === undefined}
							loading={uploading}
						>
							{uploading ? 'Đang tải lên' : 'Tải lên' }
					</Button>
				</Row>
				<Divider orientation="left">Hình ảnh đã tải lên</Divider>
				<Row gutter={16} >
					{
						images && images.map((item, index) => {
							return (
								<Col xs={24} sm={8} md={6} lg={4} key={index} style={{ marginBottom: '16px' }}>
									<Card
										hoverable
										bordered
										cover={<img alt="thumb" src={BaseUrl + item.thumbUrl} />}
									>
										<Meta
											title={
												<Checkbox checked={item.selected} onChange={(e) => onChangeCheckboxMainImage(e, index)}>
													Chọn logo
												</Checkbox>
											}
										/>
									</Card>
								</Col>
							)
						})
					}
				</Row>
			</Modal>
		</div>
	)
}
