import React, { useState, useEffect } from 'react';
import { Divider, Form, Input, Row, Col, Select, Tabs, Button } from 'antd';
import { getHotelInfo, updateHotelInfo } from '../../api/HotelApi';
import { getLocationByType } from '../../api/LocationApi';
import { parseLanguage } from '../../helpers/LanguageHelper';
import { Editor } from 'react-draft-wysiwyg';
import { EditorState, ContentState, convertToRaw } from 'draft-js';
import 'react-draft-wysiwyg/dist/react-draft-wysiwyg.css';
import draftToHtml from 'draftjs-to-html';
import htmlToDraft from 'html-to-draftjs';
import MyMapComponent from '../../components/MyMapComponent';
import SelectImageModal from './modals/SelectImageModal';

const FormItem = Form.Item;
const Option = Select.Option;
const TabPane = Tabs.TabPane;

const locationTypes = ['AREA', 'CITY', 'COUNTRY'];

function HotelInfoForm(props) {
  const [hotel, setHotel] = useState({});
  const [description, setDescription] = useState({});
  const [marker, setMarker] = useState(undefined);
  const [showModal, setShowModal] = useState(false);
  const [countries, setCountries] = useState([]);
  const [cities, setCities] = useState([]);
  const [area, setArea] = useState([]);

  useEffect(() => {
    if (props.hotelId) {
      fetchHotel(props.hotelId);
      fetchLocationByType('COUNTRY');
    }
  }, [props.hotelId])

  const fetchHotel = async (id) => {
    getHotelInfo({ hotelId: id }).then(res => {
      if (res.status === 'SUCCESS' && res.value) {
        console.log(res.value);
        setHotel(res.value);
        const descriptionObj = {};
        res.value.i18ns.map(item => {
          if (item.description) {
						descriptionObj[item.lang] = EditorState.createWithContent(ContentState.createFromBlockArray(htmlToDraft(item.description).contentBlocks));
					} else {
						descriptionObj[item.lang] = EditorState.createEmpty();
					}
        })
        setDescription(descriptionObj);
        setMarker({lat: res.value.lat, lng: res.value.lng});
      }
    })
  }

  const fetchLocationByType = async (locationType, parentId) => {
    getLocationByType({locationType, parentId}).then(res => {
      if (res.status === 'SUCCESS' && res.value) {
        if (locationType === 'COUNTRY') {
          setCountries(res.value);
        } else if (locationType === 'CITY') {
          setCities(res.value);
        } else if (locationType === 'AREA') {
          setArea(res.value);
        }
      }
    })
  }

  const onClickShowModal = () => {
    setShowModal(true);
  }

  const onCloseModal = (url) => {
    if (url) {
      props.form.setFieldsValue({ logoUrl: url });
      const data = {...hotel};
      data.logoUrl = url;
      setHotel(data);
    }
    setShowModal(false);
  }

  const onSelectCountry = (e, option) => {
    fetchLocationByType('CITY', parseInt(option.key));
  }

  const onSelectCity = (e, option) => {
    fetchLocationByType('AREA', parseInt(option.key));
  }

  const onEditorDescriptionChange = (value, lang) => {
    const descriptionObj = {...description};
    descriptionObj[lang] = value;
    setDescription(descriptionObj);
  }

  const onMarkerDragEnd = (coord) => {
    const { latLng } = coord;
    setMarker({lat: latLng.lat(), lng: latLng.lng()})
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    if (!props.hotelId) return;
    props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        console.log('Received values of form: ', values);
        const { email, hotline, logoUrl, phoneNum, ratingCount, starRanking, totalRating, title, address,
          country, city, location
        } = values;
        const i18ns = [];
				Object.entries(description).map(([key, value]) => {
					i18ns.push({
						description: draftToHtml(convertToRaw(description[key].getCurrentContent())),
						lang: key,
						title: title[key] || '',
            address: address[key] || '',
            country: country[key] || '',
            city: city[key] || '',
            location: location[key] || ''
					})
        })
        const data = {
          hotelId: props.hotelId,
          email, hotline, i18ns, lat: marker.lat, lng: marker.lng,
          logoUrl, phoneNum, ratingCount, starRanking, totalRating
        }
        updateHotelInfo(data).then(res => {
          console.log(res)
        })
      }
    });
  }

  const formItemLayout = {
    labelCol: {
      xs: { span: 24 },
      sm: { span: 6 },
    },
    wrapperCol: {
      xs: { span: 24 },
      sm: { span: 12 },
    },
  };

  const formItemLayout2 = {
    labelCol: {
      xs: { span: 24 },
      sm: { span: 3 },
    },
    wrapperCol: {
      xs: { span: 24 },
      sm: { span: 21 },
    },
  };

  const { getFieldDecorator } = props.form;

  return (
    <div>
      {/* <Divider orientation="left">Thông tin khách sạn</Divider> */}
      <Form onSubmit={handleSubmit}>
        <Row gutter={16}>
          <Col xs={24} sm={12}>
            <FormItem
              {...formItemLayout}
              label="Mã khách sạn"
            >
              {getFieldDecorator('hotelId', {
                initialValue: hotel.hotelId,
                rules: [{
                  required: true, message: 'Vui lòng nhập Mã khách sạn!'
                }],
              })(
                <Input readOnly />
              )}
            </FormItem>
          </Col>

          <Col xs={24} sm={12}>
            <FormItem
              {...formItemLayout}
              label="Điện thoại cố định"
            >
              {getFieldDecorator('hotline', {
                initialValue: hotel.hotline,
                rules: [{
                  required: true, message: 'Vui lòng nhập Số điện thoại cố định!'
                }],
              })(
                <Input />
              )}
            </FormItem>
          </Col>

          <Col xs={24} sm={12}>
            <FormItem
              {...formItemLayout}
              label="Hạng sao"
            >
              {getFieldDecorator('starRanking', {
                initialValue: hotel.starRanking
              })(
                <Select
                  placeholder="Chọn hạng sao"
                  onChange={(value) => console.log(value)}
                >
                  <Option value="1">1 star</Option>
                  <Option value="2">2 star</Option>
                  <Option value="3">3 star</Option>
                  <Option value="4">4 star</Option>
                  <Option value="5">5 star</Option>
                </Select>
              )}
            </FormItem>
          </Col>

          <Col xs={24} sm={12}>
            <FormItem
              {...formItemLayout}
              label="Điện thoại di động"
            >
              {getFieldDecorator('phoneNum', {
                initialValue: hotel.phoneNum,
                rules: [{
                  required: true, message: 'Vui lòng nhập Số điện thoại di động!'
                }],
              })(
                <Input />
              )}
            </FormItem>
          </Col>

          <Col xs={24} sm={12}>
            <FormItem
              {...formItemLayout}
              label="Loại khách sạn"
            >
              {getFieldDecorator('hotelType', {
                initialValue: hotel.hotelType
              })(
                <Select
                  placeholder="Chọn loại khách sạn"
                  onChange={(value) => console.log(value)}
                >
                  <Option value="hotel">Khách sạn</Option>
                  <Option value="resort">Khu nghỉ dưỡng</Option>
                </Select>
              )}
            </FormItem>
          </Col>

          <Col xs={24} sm={12}>
            <FormItem
              {...formItemLayout}
              label="Trang web"
            >
              {getFieldDecorator('website', {
                initialValue: hotel.website
              })(
                <Input />
              )}
            </FormItem>
          </Col>

          <Col xs={24} sm={12}>
            <FormItem
              {...formItemLayout}
              label="Logo"
            >
              {getFieldDecorator('logoUrl', {
                initialValue: hotel.logoUrl || ""
              })(
                <Input placeholder='Click để chọn logo' onClick={onClickShowModal} />
              )}
            </FormItem>
          </Col>

          <Col xs={24} sm={12}>
            <FormItem
              {...formItemLayout}
              label="Địa chỉ email"
            >
              {getFieldDecorator('email', {
                initialValue: hotel.email,
                rules: [{
                  required: true, message: 'Vui lòng nhập Địa chỉ email!'
                }],
              })(
                <Input />
              )}
            </FormItem>
          </Col>

          <Col xs={24} sm={12}>
            {
              hotel.logoUrl ?
              <div style={{ textAlign: 'center' }}>
                <img src={hotel.logoUrl} alt="logo" width='200px' height='160px' />
              </div>
              : null
            }
            
          </Col>
        </Row>
        <Divider />
        {
          hotel.i18ns ?
          <Tabs defaultActiveKey="0">
            {
              hotel.i18ns && hotel.i18ns.map((item, index) => {
                return (
                  <TabPane tab={parseLanguage(item.lang)} key={index}>
                    <Row>
                      <Col xs={24} sm={12}>
                        <FormItem
                          {...formItemLayout}
                          label="Tên khách sạn"
                        >
                          {getFieldDecorator(`title[${item.lang}]`, {
                            initialValue: item.title,
                            rules: [{
                              required: true, message: 'Vui lòng nhập Tên khách sạn!'
                            }],
                          })(
                            <Input />
                          )}
                        </FormItem>
                      </Col>
                      <Col xs={24} sm={12}>
                        <FormItem
                          {...formItemLayout}
                          label="Địa chỉ"
                        >
                          {getFieldDecorator(`address[${item.lang}]`, {
                            initialValue: item.address,
                            rules: [{
                              required: true, message: 'Vui lòng nhập Địa chỉ!'
                            }],
                          })(
                            <Input />
                          )}
                        </FormItem>
                      </Col>
                      <Col xs={24} sm={12}>
                        <FormItem
                          {...formItemLayout}
                          label="Quốc gia"
                        >
                          {getFieldDecorator(`country[${item.lang}]`, {
                            initialValue: item.country
                          })(
                            <Select
                              placeholder="Chọn quốc gia"
                              onChange={onSelectCountry}
                            >
                              {
                                countries && countries.map(country => {
                                  if (country.i18ns[0] && country.i18ns[0].lang === item.lang) {
                                    return (
                                      <Option value={country.i18ns[0].title} key={country.locationId}>
                                        {country.i18ns[0].title}
                                      </Option>
                                    )
                                  }
                                })
                              }
                            </Select>
                          )}
                        </FormItem>
                      </Col>
                      <Col xs={24} sm={12}>
                        <FormItem
                          {...formItemLayout}
                          label="Thành phố"
                        >
                          {getFieldDecorator(`city[${item.lang}]`, {
                            initialValue: item.city
                          })(
                            <Select
                              placeholder="Chọn thành phố"
                              onChange={onSelectCity}
                            >
                              {
                                cities && cities.map(city => {
                                  if (city.i18ns[0] && city.i18ns[0].lang === item.lang) {
                                    return (
                                      <Option value={city.i18ns[0].title} key={city.locationId}>
                                        {city.i18ns[0].title}
                                      </Option>
                                    )
                                  }
                                })
                              }
                            </Select>
                          )}
                        </FormItem>
                      </Col>
                      <Col xs={24} sm={12}>
                        <FormItem
                          {...formItemLayout}
                          label="Khu vực"
                        >
                          {getFieldDecorator(`location[${item.lang}]`, {
                            initialValue: item.location
                          })(
                            <Select
                              placeholder="Chọn khu vực"
                            >
                              {
                                area && area.map(a => {
                                  if (a.i18ns[0] && a.i18ns[0].lang === item.lang) {
                                    return (
                                      <Option value={a.i18ns[0].title} key={a.locationId}>
                                        {a.i18ns[0].title}
                                      </Option>
                                    )
                                  }
                                })
                              }
                            </Select>
                          )}
                        </FormItem>
                      </Col>
                      <Col xs={24}>
                        <FormItem
                          {...formItemLayout2}
                          label="Mô tả"
                        >
                          <Editor
                            editorState={description[item.lang]}
                            toolbarClassName="toolbarClassName"
                            wrapperClassName="wrapperClassName wrapperClassName-fix"
                            editorClassName="editorClassName editorClassName-fix"
                            onEditorStateChange={(value) => onEditorDescriptionChange(value, item.lang)}
                          />
                        </FormItem>
                      </Col>
                    </Row>
                  </TabPane>
                )
              })
            }
          </Tabs>
          : null
        }

        <Divider orientation="left">Vị trí trên Google Map</Divider>
        {
          marker ? 
          <MyMapComponent isMarkerShown 
            lat={marker.lat} 
            lng={marker.lng} 
            onMarkerDragEnd={onMarkerDragEnd.bind(this)}
          /> 
          : null
        }

        <SelectImageModal showModal={showModal} onCloseModal={onCloseModal} hotelId={props.hotelId} />
        <Button type='primary' htmlType='submit' size='large' style={{ marginTop: '15px' }}>
          Lưu
        </Button>
      </Form>
    </div>
  )
}

const HotelInfo = Form.create()(HotelInfoForm);
export default HotelInfo;